.main {
  display: flex;
  flex-direction: column;
  width: 400px;
  min-width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 5px 30px 20px;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 0 5px rgba(0,0,0,0.2);
}
.main h2 {
  text-align: center;
}

.main h4 {
  margin-block-start: 0;
  margin-block-end: 0;
}

.main p {
  text-align: center;
}

.button {
  margin: 5px 0;
  font-size: 12pt;
}

.message {
  color: black;
}

.error {
  color: red;
}