.card {
  display: flex;
  flex-direction: row;
  border-radius: 4px;
  background-color: white;
  border: solid 1px rgba(0,0,0,0.3);
  margin: 10px 0;
}

.thumbnail {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex: 0 0 150px;
  height: 150px;
  align-items: center;
  overflow: hidden;
  position: relative;
}
.addImageIcon {
  width: 130px;
  height: 130px;
  color: #e9e9e9;
  cursor: pointer;
}

.img {
  width: 150px;
}

.removeButton {
  background-color: #e9e9e9;
  padding: 5px;
  font-size: 1.2em;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  right: 4px;
  top: 4px;
  min-width: 0;
}

.questionNum {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  font-weight: bold;
  padding: 5px 0;
  text-align: center;
  margin-bottom: 5px; 
  width: 100px;
}

.info {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.rightTopContainer {
  padding: 10px 20px;
  width: 100%;
  flex: 3;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.rightTopContainer h3 {
  margin-block-start: 0;
}

.gameStatus {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-right: 5px;
}

.copyButton {
  margin-right: -10px;
}

.confrimButton {
  margin-right: 6px;
}

.metadata {
  padding: 0 20px;
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: solid 1px rgba(0, 0, 0, 0.3);
}

@media screen and (max-width: 600px) {
  .buttons {
    display: none;
  }
  .rightTopContainer {
    padding: 10px 5px;
    width: 100%;
    flex: 3;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .gameButtons {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}


.button {
  margin: 2px 10px;
}

