.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 300px;
  background-color: white;
  box-shadow: 0 0 5px rgba(0,0,0,0.2);
}


.topBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 25px;
  width: 100%;
  font-size: 14pt;
  font-weight: bold;
  padding-left: 20px;
  border-bottom: solid 1px #f0f0f0;
}

.editIcon {
  margin-left: 3px;
  cursor: pointer;
}

.nameInput {
  font-size: 14pt;
  padding: 10px 10px;
}

.inputContainer {
  margin: 1em 0;
}

.questionContainer {
  width: 810px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

@media screen and (max-width: 900px) {
  .questionContainer {
    width: 540px;
  }
}

@media screen and (max-width: 600px) {
  .questionContainer {
    width: 270px;
  }
}

