.moreOptions:hover {
  opacity: 0.6;
}

.moreOptions {
  width: 40px;
  height: 26px;
  cursor: pointer;
}

.moreOptionsWrapper {
  position: relative;
}

.icon {
  display: inline-block;
  vertical-align: middle;
  width: 32px;
  height: 32px;
}

.dropdown {
  width: 165px;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0px 0 3px 0px rgba(0,0,0,0.2);
  position: absolute;
  z-index: 1;
  right: 0;
}


.item {
  margin: 8px;
}

.item:hover {
  background-color: rgba(0,0,0,0.05);
}

.innerItem {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px 5px;
}

.content {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-weight: normal;
}

.content p {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-left: 12px;
  font-size: 12pt;
}

@media screen and (max-width: 600px) {

  .moreOptions {
    display: inline;
  }
}

@media screen and (min-width: 601px) {
  .moreOptionsWrapper {
    display: none;
  }
  .moreOptions {
    display: none;
  }
}