.modalActive {
  position: fixed;
  transform: translate(-50%, -50%) scale(1);
  transition: 200ms ease-in-out;
  top: 50%;
  left: 50%;
  background-color: white;
  border-radius: 5px;
  box-shadow: -3px 3px 8px rgb(155, 155, 155);
  width: 500px;
  max-width: 80%;
  border: solid 1px black;
  z-index: 100;
}


.modalHidden {
  position: fixed;
  transform: translate(-50%, -50%) scale(0);
  transition: 200ms ease-in-out;
  top: 50%;
  left: 50%;
  background-color: white;
  border-radius: 5px;
  box-shadow: -3px 3px 8px rgb(155, 155, 155);
  width: 500px;
  max-width: 80%;
  border: solid 1px black;
  z-index: 100;
}







.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom: solid 1px black;
}



.header .title {
  font-size: 1.25rem;
  font-weight: bold;
}

.header .closeButton {
  cursor: pointer;
  border: none;
  background: none;
  outline: none;
  font-size: 2rem;
}

.body {
  padding: 10px 15px;
  min-height: 100px;
  word-wrap: break-word;
}
