nav {
  height: 60px;
  background-color: white;
  width: 100%;
  padding: 10px 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-bottom: solid 1px #f0f0f0;
}

.logo {
  font-size: 24pt;
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.path {
  color: unset;
  text-decoration: none;
  display: inline-block;
}

.normal {
  font-size: 12pt;
  margin: 0 5px;
}

.hidden {
  display: none;
}