.card {
  margin: 10px;
  padding: 5px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 250px;
  height: 250px;
  background-color: rgb(234, 244, 252);
}

.card:hover {
  background-color: rgb(234, 244, 252);
}

.title {
  width: 100%;
  margin-block-start: 0.5em;
  margin-block-end: 0;
}

.question {
  margin-block-start: 0;
  margin-block-end: 0;

}

.mediaContainer {
  width: 150px;
  height: 120px;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.image {
  width: 150px;
  height: 120px;
}

.icon {
  width: 150px;
  height: 120px;
  color: #61dafb;
}

.buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.button {
  margin: 0 10px;
}