.topBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 25px;
  width: 100%;
  font-size: 14pt;
  font-weight: bold;
  padding-left: 20px;
  border-bottom: solid 1px #f0f0f0;
}

.buttons {
  width: 200px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.addGame {
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 14pt;
}


.gameContainer {
  width: 90%;
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

